import { Button } from '@material-ui/core'
import { useSnackbar } from 'notistack'
export default function Lite() {
  const { enqueueSnackbar } = useSnackbar()
  return (
    <div>
      <Button
        onClick={() => {
          console.log(3)
          enqueueSnackbar({
            type: 'failed',
            title: 'Fail.',
            message: 'Your transaction failed.',
          })
        }}
      >
        12345
      </Button>
    </div>
  )
}
